import React from 'react';
import {Table, Container, Button} from 'react-bootstrap';


const Home = (props) => {
    const propList= ["Who would win in General Elections", "Who is suitable for MP candidate", 
    "Who would win in Student Elections", "Who has more chance of winning in Presidental Elections", 
    "Who will be selected as Committee Leader", "Who will be next Panchayat",
    "Who is the next Municipal Officer"]
  return (
    <div>
        <div class="alert alert-primary justify-content-center d-flex" role="alert">
        Work in Progress.
        </div>
        <Container>
            <Table style={{margin: "5vh"}} striped bordered hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>List of Polls</th>
                        <th>Go to Poll</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        propList.map( (el, index) =>{
                            return(
                            <tr key={index}>
                                <td>{index+1}</td>
                                <td>{el}</td>
                                <td><Button onClick={() => props.changeCandidates(el)}>Go to Poll</Button></td>
                            </tr>
                        )})
                    }

                </tbody>
            </Table>
        </Container>
    </div>
  )
}

export default Home
