import React, {useState} from 'react'
import {Container, Row, Col, Button} from 'react-bootstrap'
import LoadingCircles from '../assets/loadinglogo.svg'

const PollingStation = (props) => {
    const [candidate1url, changeCandidate1Url] = useState(LoadingCircles)
    const [candidate2url, changeCandidate2Url] = useState(LoadingCircles)
    const [showresults,changeResultsDisplay] = useState(false);
    return (
    <Container>
        <Row>
            <Col className='justify-content-center d-flex'>
                <Container>
                    <Row style={{marginTop:'5vh', backgroundColor: '#A9CFCF'}} > 
                        <div
                            style={{
                                display: 'flex',
                                justifyContent: 'center',
                                padding: '3vw'
                            }}
                            >
                                <img 
                                    style={{height:'35vh', width:'20vw'}}
                                    src = {candidate1url}
                                ></img>
                        </div>
                    </Row>
                   {showresults? <Row style={{
                        marginTop:'5vh'
                    }} className='justify-content-center d-flex'>
                        <div style={{
                            display: "flex",
                            justifyContent: "center",
                            fontSize:'8vw',
                            padding: '10px',
                            backgroundColor: '#A9CFCF',
                        }}>
                            3
                        </div>
                    </Row>: null}
                    <Row style={{marginTop:'5vh'}}  
                    className='justify-content-center d-flex'   >
                        <Button className='btn btn-success' >Vote</Button>
                    </Row>
                </Container>
            </Col>

            <Col className='justify-content-center d-flex align-items-center' >
                <div style={{
                    display:"flex",
                    justofyContent:"center",
                    backgroundColor: '#A9CFCF',
                    height:'20vh',
                    alignItems:"center",
                    padding:'2vw',
                    textAlign:"center"
                }} >
                        Who would win?
                </div>
            </Col>
            <Col className='justify-content-center d-flex'>
                <Container>
                    <Row style={{marginTop:'5vh', backgroundColor: '#A9CFCF'}} > 
                        <div
                            style={{
                                display: 'flex',
                                justifyContent: 'center',
                                padding: '3vw'
                            }}
                            >
                                <img 
                                    style={{height:'35vh', width:'20vw'}}
                                    src = {candidate2url}
                                ></img>
                        </div>
                    </Row>
                   {showresults? <Row style={{
                        marginTop:'5vh'
                    }} className='justify-content-center d-flex'>
                        <div style={{
                            display: "flex",
                            justifyContent: "center",
                            fontSize:'8vw',
                            padding: '10px',
                            backgroundColor: '#A9CFCF',
                        }}>
                            3
                        </div>
                    </Row>: null}
                    <Row style={{marginTop:'5vh'}}  
                    className='justify-content-center d-flex'   >
                        <Button className='btn btn-success' >Vote</Button>
                    </Row>
                </Container>
            </Col>

        </Row>
    </Container>
  )
}

export default PollingStation
